:root {
    --font: "Montserrat", sans-serif !important;
    --color: #fff;
    --colorOpacity: #ffffffe3;
    --colorInvert: #000;
    --colorBlack: #2c2e3e;
    --helpColor: #ffffffad;
    --colorSecond: #adadad;
    --bgBody: #121212;
    --bgSecondHelper: #181818;
    --borderButtonSpecialColor: #7873a3; /* #ff6347*/
}

