.platforms {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
}
.MuiCardContent-root button.showURL {
    justify-content: center !important;
    width: min-content !important;
}
.backLinks {
    background-color: var(--bgSecondHelper) !important;
    border-radius: 10px !important;
    padding: 16px !important;
}

.MuiButton-root {
    color: var(--color) !important;
    border: 3px solid var(--color) !important;
    border-radius: 0px !important;
    font-size: 24px !important;
    transition: all 0.15s ease;
}
.MuiButton-root:hover {
    background-color: var(--color) !important;
    color: var(--colorInvert) !important;
}
.MuiButton-root:hover path {
    color: var(--colorInvert) !important;
}
.paper {
    background-color: var(--bgBody) !important;
    color: #009300 !important;
    border-radius: 10px;
    transition: all 0.15s ease;
}
.specialButton {
    border: 3px solid var(--borderButtonSpecialColor) !important;
}
.specialButton:hover {
    background: #eb3349;
    background: -webkit-linear-gradient(to right, #f45c43, #eb3349);
    background: linear-gradient(to right, #f45c43, #eb3349);
    background: linear-gradient(to right, #8f8abe, #7873a3);
    background: -webkit-linear-gradient(to right, #8f8abe, #7873a3);
    color: var(--colorWhite) !important;
    border: 3px solid var(--borderButtonSpecialColor) !important;
}
.bg-js {
    background-color: #bba500 !important;
}
.bg-ts {
    background-color: #2f74c0 !important;
}
.bg-node {
    background-color: #74ab5f !important;
}
.bg-react {
    background-color: #33afd1 !important;
}
